@charset "utf-8";

$mainColor: #5f0080;
$textColor: #333333;
$whiteColor: #ffffff;
$grayColor: #999999;

header {
  .topBanner {
    background: $mainColor;
    color: $whiteColor;
    text-align: center;
    padding: 14px 0;
    font-weight: 100;
    font-size: 12px;
  }
  .topLnb {
    width: 1050px;
    margin: 0 auto;
    display: flex;
    justify-content: end;
    font-size: 12px;
    padding: 14px 0px;

    li {
      padding: 0px 10px;
      border-right: 1px solid $grayColor;
      a {
        color: $textColor;
        text-decoration: none;
      }
      .mainColor {
        color: $mainColor;
      }
      &:last-child {
        padding-right: 0;
        border: none;
      }
    }
  }

  .headerLogo {
    width: 1050px;
    margin: 0 auto;
    display: flex;
    justify-content: baseline;
    padding-bottom: 10px;
    h1 {
      width: 100px;
    }
    button {
      background: transparent;
      border: none;
      font-size: 18px;
      font-weight: bold;
      margin-left: 10px;

      &.marketBtn {
        color: $mainColor;

        &::after {
          content: "";
          position: absolute;
          width: 1px;
          height: 14px;
          margin-top: 5px;
          margin-left: 11px;
          background-color: rgb(226, 226, 226);
        }
      }
      &.beautyBtn {
        color: $grayColor;
        &:hover {
          color: $mainColor;
        }
      }
    }
  }

  .searchBox {
    width: 1050px;
    margin: 0 auto;
    position:relative;
    div {
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      -webkit-box-pack: justify;
      justify-content: space-between;
      position: absolute;
      left: 325px;
      top: -55px;
      width: 400px;
      height: 48px;
      padding-left: 14px;
      border: 1px solid rgb(95, 0, 128);
      border-radius: 6px;
      background-color: rgb(255, 255, 255);
      box-shadow: rgb(247, 247, 247) 0px 0px 0px 1px inset;

      input{
        border:none;
        width: 100%;
        font-size: 16px;
      }

      button{
        border: none;
        background: transparent;
        color:$mainColor;
        font-size: 24px;
        padding-right: 10px;
        cursor: pointer;
      }
    }
  }

  .iconBtns{
    width: 1050px;
    margin: 0 auto;
    position:relative;
    div{
      display: flex;
      -webkit-box-align: center;
      align-items: center;
      position: absolute;
      right: -6px;
      top: -49px;
      a{
        font-size: 36px;
        color:$textColor;
        margin-left:20px;
        &:hover{
          color:$mainColor;
        }
      }
    }
  }

  .gnb{
    width: 1050px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .catogory{
      color:$textColor;
      cursor: pointer;
      &:hover{
        color:$mainColor;
      }
    }
    ul{
      display: flex;
      li{
        margin:20px 40px;
        a{
          text-decoration: none;
          color:$textColor;
          &:hover{
            text-decoration: underline;
            color:$mainColor;
          }
        }
      }
    }
    .delivery{
      font-size: 12px;
      color: $textColor;
      div{
        border:1px solid #ddd;
        border-radius: 20px;
        padding: 5px 10px;
        span{
          color:$mainColor;
        }
      }
    }
  }
}
