main .slide img{
  width:100%;
}

.product_list{
  margin: 50px auto;
  width: 1050px;
}
.product_list h2{
  font-size: 28px;
  font-weight: 600;
  line-height: 40px;
  color:#333;
}
.product_list > p{
  color:#999;
}
.product_list h2, .product_list > p{
  text-align: center;
}

.product_list ul{
  margin: 20px 0;
  display:flex;
  justify-content: space-between;
}
.product_list ul li{width: 25%; margin-right: 20px;}
.product_list ul li:last-child{margin-right: 0px;}
.product_list ul li img{width: 100%;}
.product_list ul li .name{
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin: 15px 0px;
}
.product_list ul li a{
  display: block;
  border:1px solid #ddd;
  color:#333;
  text-align: center;
  text-decoration: none;
  padding: 10px 0px;
  margin-top:5px;
}
.product_list ul li .price{
  color:#b5b5b5;
  text-decoration: line-through;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.product_list ul li .price2{
  font-weight: bold;
  color:#333;
}
.product_list ul li .price2 .sale{
  margin-right: 20px;
  color:orangered;
}

footer{
  background: #333;
  padding: 10px 0px;
}
footer > address{
  font-style:normal;
  color:#fff;
  text-align: center;
}